.button {
  display: inline-block;
  position: relative; }
  .button.full-width .button--btn {
    width: 100%; }
  .button--btn {
    border-radius: 2px;
    -webkit-box-shadow: none;
            box-shadow: none;
    cursor: pointer;
    padding: 0.99rem; }
  .button--outline {
    background-color: transparent;
    border-color: #4DA1FF;
    color: #4DA1FF; }
    .button--outline:hover {
      background-color: #4192EC;
      border-color: #4192EC;
      color: #FFFFFF; }
  .button--skinny {
    padding: 0.69rem; }
  .button--thick {
    padding: 1.65rem;
    font-size: 1.6rem; }
  .button--solid {
    background-color: #4192EC;
    border-color: #4192EC;
    color: #FFFFFF; }
    .button--solid:hover {
      background-color: transparent;
      color: #4192EC; }
  .button--with-icon {
    padding-right: 1.5rem;
    padding-left: 3.3rem; }
  .button--icon {
    left: 10px;
    opacity: 0.75;
    stroke-width: 0.5;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); }
  .button--text-only {
    border: none;
    background: transparent;
    color: #888; }
    .button--text-only:hover {
      color: #4192EC; }
