.text-input {
  display: inline-block;
  width: 100%;
  position: relative; }
  .text-input label {
    color: #888;
    font-size: 1.1rem;
    text-transform: uppercase;
    padding-left: 2px; }
  .text-input input {
    background-color: transparent;
    font-size: 1.6rem;
    margin-top: 1.2rem;
    padding: 1.35rem;
    width: 100%; }
    .text-input input:focus {
      outline: none; }
  .text-input-outline input {
    border: solid 1px #E7E9EA;
    border-radius: 4px; }
    .text-input-outline input:focus {
      border-radius: 4px;
      border: solid 1px #4192EC; }
  .text-input-no-outline input {
    border: none;
    border-bottom: solid 1px transparent; }
    .text-input-no-outline input:focus {
      border-bottom: solid 1px #4192EC; }
    .text-input-no-outline input.withIcon {
      padding-left: 3rem; }
  .text-input--icon {
    position: absolute;
    width: 16px;
    left: 0;
    top: 62%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); }
  .text-input--button {
    position: absolute;
    right: 10px;
    top: 70%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); }
  .text-input ::-webkit-input-placeholder {
    color: #888; }
  .text-input :-ms-input-placeholder {
    color: #888; }
  .text-input ::-ms-input-placeholder {
    color: #888; }
  .text-input ::placeholder {
    color: #888; }
