.content--page {
  margin-left: 112px;
  margin-right: 250px;
  margin-top: 112px;
  background-color: #f4f8f9;
  height: 100vh; }

/*
 * Defaults Styles
 **
 * HTML Reset
 * Base styles
 */
/********************************************************** Viewport */
@-webkit-viewport {
  width: device-width; }

@-moz-viewport {
  width: device-width; }

@-ms-viewport {
  width: device-width; }

@-o-viewport {
  width: device-width; }

@viewport {
  width: device-width; }

/********************************************************** Box Sizing *
   Box Sizing
   Apply a natural box layout model to all elements - http://paulirish.com/2012/box-sizing-border-box-ftw/
   border-box polyfill for IE 6/7: https://github.com/Schepp/box-sizing-polyfill#readme

   http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
 */
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 62.5%;
  overflow-y: auto; }

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

/********************************************************** General */
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden; }

img {
  max-width: 100%; }

a {
  color: currentColor;
  text-decoration: underline;
  outline: 0; }
  a:focus {
    outline: 0; }
  a:hover, a:active {
    outline: 0; }

button:focus, button:active {
  outline: 0; }

/********************************************************** Typography */
body {
  background: #f9fcff;
  color: #323C47;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

p {
  line-height: 1.5;
  margin: 0;
  padding: 0 0 1.5rem; }

ul,
ol,
dl {
  line-height: 1.5;
  list-style: none;
  margin: 0 0 0 1.5rem;
  padding: 0 0 0.75rem; }

p ul,
ul ul,
ol ul,
p ol,
ul ol,
ol ol {
  padding-bottom: 0; }

strong {
  font-weight: bold; }

code {
  font-size: 1.6rem;
  line-height: 1.5; }

input,
textarea {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

/**********************************************************
   Clearfix
   css/app/utilities/_clearfix
 */
/*
  *
  * http://nicolasgallagher.com/micro-clearfix-hack/
  *
  * For modern browsers
  * 1. The space content is one way to avoid an Opera bug when the
  *    contenteditable attribute is included anywhere else in the document.
  *    Otherwise it causes space to appear at the top and bottom of elements
  *    that are clearfixed.
  * 2. The use of `table` rather than `block` is only necessary if using
  *    `:before` to contain the top-margins of child elements.
  */
.clearfix:before,
.clearfix:after {
  content: ' ';
  /* 1 */
  display: table;
  /* 2 */ }

.clearfix:after {
  clear: both; }

/**
  * For IE 6/7 only
  * Include this rule to trigger hasLayout and contain floats.
  */
.clearfix {
  zoom: 1; }

.nowrap {
  white-space: nowrap; }

.full-height {
  height: 100%; }

.full-width {
  width: 100%; }
