.upload-card {
  margin: 4.5rem 0; }
  .upload-card--file-size {
    color: #888;
    font-size: 1.4rem;
    margin: 1.5rem 0; }
  .upload-card--unit {
    text-transform: uppercase;
    font-size: 1.1rem; }
  .upload-card--file-name {
    margin: 1.5rem 0; }
  .upload-card--link {
    color: #4DA1FF;
    font-weight: 700;
    font-size: 1.1rem; }
    .upload-card--link.incomplete {
      color: #E7E9EA; }
      .upload-card--link.incomplete a {
        cursor: not-allowed; }
    .upload-card--link a {
      cursor: pointer;
      text-decoration: none; }
